.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #000;
  margin-top: 40px;
}
.header__logo {
  font-weight: 700;
  font-size: 32px;
  line-height: 170%;
}

.nav {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 600px;
}
.nav__item_button {
  color: #fff;
  padding: 14px 32px;
  background-color: #2C3878;
}